import React,{useState,useEffect} from 'react'
import {
  
  CButton,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CDataTable,
  CRow
} from '@coreui/react'
import { Link, useParams } from 'react-router-dom';
import API from 'src/api';

const UnitList = () => {
  
  let {id}  = useParams();
  const fields = ['title','edit','delete'];
  const [unitData,setUnitData] = useState([{}]);
  const [isLoading,setIsLoading] = useState(false);

  useEffect(()=>{
    getData();
  },[]);

  async function getData(){
    try {
      setIsLoading(true);    
      var response = await API.get("/unit/list?topicId="+id);  
      setUnitData(response.data.data);
      setIsLoading(false);
    } catch (error) {   
      alert("Try Again");   
    }
  }

  const deleteUnit = async (id)=>{
    try {      
      setIsLoading(true);    
      var response = await API.delete("/unit/"+id);  
      if(response.status === 201)
      {
        var data = unitData.filter((a)=>{ return a._id !== id});
        setUnitData(data);
      }
      setIsLoading(false);
    } catch (error) {
      alert("api error");
    }
  }

  return (
    <>
      <div className="">
        <CRow className="p-2 ml-auto">
        </CRow>
        <CRow>
        <CCol xs="12">
          <CCard>
            <CCardHeader>
              <span className="h4">
                Unit List
              </span>
              <Link to={{pathname:`/addunit/${id}`}} className="btn btn-primary float-right">              
                Add
              </Link>
            </CCardHeader>
            <CCardBody>
            {isLoading ? <div>Loading...</div> : 
            <CDataTable
              items={unitData}
              fields={fields}
              itemsPerPage={5}
              loading={isLoading}
              pagination
              scopedSlots = {{
                'edit':
                  (item)=>(
                    <td>

                    <Link to={{pathname:`/editunit/${item._id}`}} className="btn btn-info"> 
                      {/* <div className="btn btn-info"> */}
                      Edit                    
                      {/* </div> */}
                    </Link>
                    </td>
                  ),
                  'delete':
                  (item)=>(
                    <td>
                      <CButton className="btn btn-danger" onClick={(e)=> {deleteUnit(item._id);}}>Delete</CButton>                 
                    </td>
                  ),

              }}
            />
}
            </CCardBody>
          </CCard>
        </CCol>
        </CRow>
        {/* <CButton className="btn btn-primary float-right" onClick={addButton}>
          Add
        </CButton> */}
      </div>
    </>
  )
}

export default UnitList
